const fs = require('fs')
const { createBundleRenderer } = require('vue-server-renderer')

const generateErrorMessage = ({ error, data, hideErrorMessage, errorType = 'Error' }) => {
  const consoleLogMessage = `
    [SSR] ${errorType}
    [SSR] Data Provided:
    ${JSON.stringify(data, null, 2)}
    [SSR] Error Message:
    ${error.stack}
  `

  const htmlMessage = `
    <p style="font-weight: bold;">${errorType}</p>
    <p>Data Provided</p>
    <pre>${JSON.stringify(data, null, 2)}</pre>
    <p>[SSR] Error Message</p>
    <pre>${error.stack}</pre>
  `

  const html = `
    <div class="oni-html-renderer-error">
      ${hideErrorMessage ? '' : htmlMessage}
      <script>console.log(${consoleLogMessage})</script>
    </div>
  `

  return html
}

module.exports = function setup (config = {}) {
  const { bundlePath, watch, hideErrorMessage } = config

  let renderer
  const setupRenderer = () => {
    try {
      const serverBundle = fs.readFileSync(bundlePath, 'utf-8')
      renderer = createBundleRenderer(JSON.parse(serverBundle))
    } catch (error) {
      renderer = {
        renderToString: () => generateErrorMessage({
          errorType: 'Renderer Setup Error',
          error,
          data: config,
          hideErrorMessage
        })
      }
    }
  }
  setupRenderer()

  if (watch) {
    require('chokidar')
      .watch(bundlePath)
      .on('change', setupRenderer)
  }

  const renderHtml = (ssrData, callback) => {
    const renderComponent = component => renderer.renderToString(component)
      .catch(error => generateErrorMessage({
        errorType: 'Error rendering HTML',
        error,
        data: ssrData,
        hideErrorMessage
      }))
      .then(html => callback ? callback(null, html) : html)

    const renderMultipleComponents = components => Promise.all(components.map(renderComponent))
      .then(htmls => htmls.join(''))
      .then(html => callback ? callback(null, html) : html)

    return Array.isArray(ssrData)
      ? renderMultipleComponents(ssrData)
      : renderComponent(ssrData)
  }

  return renderHtml
}
